import React from "react";
import { Jsme } from 'jsme-react'

import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection
} from "streamlit-component-lib";

interface JsmeArgs {
  width: string;
  height: string;
  init_smiles: string;
}


export class StJsme extends StreamlitComponentBase {
  state = {
    smile: "",
    n:0
  };

  logSmiles(smiles: string) {
    console.log(smiles)
    this.state.smile = smiles
  }

  returnSmile() {
    Streamlit.setComponentValue([this.state.smile,this.state.n])
    this.state.n++
  }

  render() {
    const { width, height, init_smiles }: JsmeArgs = this.props.args

    return (
      <html>
      <body>
        <div style={{ height: height, marginBottom: '10px' }}>
          <Jsme height={height} width={width} smiles={init_smiles} options="oldlook, star" onChange={this.logSmiles.bind(this)} />
        </div>
        <button className='myButton' onClick={this.returnSmile.bind(this) }>Add Molecule</button>
      </body>
      </html>
    )
  }
}

// Link the component to Streamlit JS events.
export default withStreamlitConnection(StJsme);